@use "variables"as var;
@use "mixins"as mix;


.cart-content {
    margin-top: 30px;

    .header {
        padding: 20px 0;
        // font-size: 30px;
        @include mix.media(font-size, $xs: 24px, $lg: 30px);



        h2 {
            font-weight: bold;
        }

        .process {
            @include mix.media(letter-spacing, $xs: 0, $md: 10px);
            @include mix.media(opacity, $xs: 0, $md: 1);
            @include mix.media(font-size, $xs: 16px, $lg: 20px);

            .bold {
                font-weight: bold;
                padding-top: 10px;
                letter-spacing: 10px;
            }

            .light {
                color: #ccc;
            }
        }
    }

    .cart-list {
        padding: 20px 0;
        border-top: solid 2px #000;
        @include mix.media(box-shadow, $xs: none, $md: 3px 3px 15px rgba(80, 93, 104, 0.18));
        align-items: center;
        @include mix.media(border-radius, $xs: 0, $md: 8px);
        @include mix.media(margin, $md: 1.5rem 0);

        h2 {
            // font-size: 24px;
            @include mix.media(font-size, $xs: 20px, $lg: 24px);

        }

        .checkbox {
            // padding-top: 50px;
            width: 20px;
            height: 20px;

        }
    }

    .cart-body {
        position: relative;
        @include mix.media(border-bottom, $xs: solid 1px #ccc, $md: none);
        @include mix.media(border-radius, $xs: 0, $md: 8px);
        @include mix.media(box-shadow,
            $xs: 0,
            $md: 3px 3px 15px rgba(80, 93, 104, 0.18));
        @include mix.media(padding, $xs: 1.5rem 0, $md: 20px);
        @include mix.media(padding-bottom, $xs: 10px);
        @include mix.media(margin, $md: 1.5rem 0);
        align-items: center;


        .checkbox {
            // padding-top: 50px;
            width: 20px;
            height: 20px;
            align-items: center;

        }

        h2 {
            @include mix.media(font-size, $xs: 20px, $lg: 24px);
            color: $gray-blue;
        }

        h3 {
            @include mix.media(font-size, $xs: 20px, $md: 24px);
        }

        .icon {
            width: 20px;
            height: 20px;
            // padding: 20px;
            @include mix.media(padding, $xs: 0, $md: 20px);

        }
    }

    .review-card-img {
        @include mix.media(width, $xs: 7rem, $md: 150px);

        .ratios {
            @include mix.ratio(1, 1);

            img {
                border-radius: 8px;
                object-fit: cover;
            }
        }
    }

    .cart-total {
        padding-top: 50px;
        text-align: right;

        .price {
            @include mix.media(border-radius, $xs: 0, $md: 8px);
            @include mix.media(box-shadow,
                $xs: 0,
                $md: 3px 3px 15px rgba(80, 93, 104, 0.18));
            border-top: solid 2px #000;
            padding-top: 20px;

            h4 {
                @include mix.media(font-size, $xs: 18px, $lg: 20px);
            }

            .payment-price {
                color: rgb(209, 82, 82);
            }
        }
    }

    .button {
        margin-top: 100px;
        @include mix.media(text-align, $xs: center, $md: right);

        .checkout {
            @include mix.media(width, $xs: 150px, $md: 200px);

            height: 50px;
            background: #000;
            color: #fff;
            border-radius: 20px;
            margin-right: 20px;
        }

        .checkout:hover {
            background: rgb(255, 255, 255);
            color: rgb(0, 0, 0);
            box-shadow: 5px 5px 5px #000;
            cursor: pointer;
        }

        .shopping {
            @include mix.media(width, $xs: 150px, $md: 200px);
            height: 50px;
            background: rgb(255, 255, 255);
            color: rgb(0, 0, 0);
            border-radius: 20px;
        }

        .shopping:hover {
            background: rgb(0, 0, 0);
            color: rgb(255, 255, 255);
            box-shadow: 5px 5px 5px rgb(180, 180, 180);
            cursor: pointer;

        }
    }
}





